.headerContainer{
    display: inline-block;
    text-align: left;
    min-width: 440px;
    width:90%;
    background-color:green;
    padding: 20px;
    position: absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.headerText{
    color: white;
    margin: 0 ;
}

.nameHeader{
color:red;
font-size: 90px;

}

.descHeader{
    color:pink;
font-size: 90px;
}



@media screen and (max-width: 600px) {
    .nameHeader{
        font-size: 50px;
        }
        
        .descHeader{
        font-size: 50px;
        }
  }